"use client";

import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

interface RegisterPopupProps {
  onClose: () => void;
  isVisible: boolean;
}

export default function RegisterPopup({ onClose, isVisible }: RegisterPopupProps) {
  if (!isVisible) return null;

  return (
    <div
      className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-70 z-50"
      role="dialog"
      aria-labelledby="register-title"
      aria-describedby="register-description"
      onClick={onClose} // Close popup when clicking outside
    >
      <div
        className="bg-white p-6 rounded-lg shadow-lg w-full max-w-md mx-4 sm:mx-auto relative"
        onClick={(e) => e.stopPropagation()} // Prevent closing when clicking inside the popup
      >
        {/* Close Button */}
        <button
          className="absolute top-4 right-4 text-gray-500 hover:text-gray-700"
          onClick={onClose}
          aria-label="Close"
        >
          ✕
        </button>

        {/* Register Form */}
        <Card className="w-full">
          <CardHeader>
            <CardTitle id="register-title" className="text-2xl font-bold">
              Register
            </CardTitle>
            <CardDescription id="register-description" className="text-gray-500">
              Enter your information to create an account
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            {/* Full Name Input */}
            <div className="space-y-2">
              <Label htmlFor="full-name" className="text-sm font-medium text-gray-700">
                Full Name
              </Label>
              <Input
                id="full-name"
                placeholder="John Doe"
                required
                className="mt-1"
              />
            </div>

            {/* Email Input */}
            <div className="space-y-2">
              <Label htmlFor="email" className="text-sm font-medium text-gray-700">
                Email
              </Label>
              <Input
                id="email"
                type="email"
                placeholder="me@example.com"
                required
                className="mt-1"
              />
            </div>

            {/* Password Input */}
            <div className="space-y-2">
              <Label htmlFor="password" className="text-sm font-medium text-gray-700">
                Password
              </Label>
              <Input
                id="password"
                type="password"
                placeholder="Enter your password"
                required
                className="mt-1"
              />
            </div>

            {/* Confirm Password Input */}
            <div className="space-y-2">
              <Label htmlFor="confirm-password" className="text-sm font-medium text-gray-700">
                Confirm Password
              </Label>
              <Input
                id="confirm-password"
                type="password"
                placeholder="Re-enter your password"
                required
                className="mt-1"
              />
            </div>

            {/* Register Button */}
            <Button className="w-full bg-green-600 hover:bg-green-700 text-white">
              Register
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}